/** @jsxImportSource @emotion/react */
import React from 'react';
import { Layout, Space } from 'antd';
import { css } from "@emotion/react";
import AppContent from "components/nav/AppContent";
const { Header, Footer, Sider, Content } = Layout;

const DefaultLayout = () => (
  <Space
    direction="vertical"
    style={{
      width: "100%",
      height: "100%"
    }}
    size={[0, 48]}
  >
    <Layout>
      <Header css={css`
                text-align: center;
                color: '#fff';
                height: 64px;
                padding-inline: 50px;
                line-height: 64px;
                background-color: #7dbcea;
              `}>
                IGAWorks
      </Header>
      <Content css={css`
                  text-align: center;
                  height: 100%;
                  background-color: #fff;
                `}>
        <AppContent />
      </Content>
    </Layout>
  </Space>
);
export default React.memo(DefaultLayout);